import { Alarms } from '@app/alarms/shared';
import {
  LocalStorageKey,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { analytics } from '@background/analytics/ga';

export type Extension = {
  id: string;
  name: string;
};

export type ExtensionInfo = {
  id: string;
  name: string;
  description?: string;
  enabled: boolean;
  version?: string;
  permissions?: string[];
  usesProxy?: boolean;
  isInternalExtension?: boolean;
};

export type DetectedConflictingExtensionsType = 'internal' | 'proxy' | 'vpn';

export type DetectedConflictingExtensions = {
  hasConflictingExtension: boolean;
  type: DetectedConflictingExtensionsType;
  conflictingExtensionId?: string;
  conflictingExtensionName?: string;
  conflictingExtensionIcon?: string;
};

export type ExtensionMonitoringData = {
  proxy: DetectedConflictingExtensions;
  internal: DetectedConflictingExtensions;
  vpn: DetectedConflictingExtensions;
};

function parseExtensions(extensionsString: string | undefined): Extension[] {
  try {
    if (!extensionsString) {
      return [];
    }

    return JSON.parse(extensionsString);
  } catch (error) {
    console.error('Error parsing extensions:', error);
    return [];
  }
}

function getInitialData(): {
  vpnExtensions: Extension[];
  adBlockers: Extension[];
  internalExtensions: Extension[];
} {
  const vpnExtensionsString = process.env.VPN_EXTENSIONS;
  const adBlockersString = process.env.AD_BLOCKERS;
  const internalExtensionsString = process.env.INTERNAL_EXTENSIONS;

  return {
    vpnExtensions: parseExtensions(vpnExtensionsString),
    adBlockers: parseExtensions(adBlockersString),
    internalExtensions: parseExtensions(internalExtensionsString),
  };
}

export const knownExtensions = getInitialData();

async function detectProxyController(): Promise<DetectedConflictingExtensions> {
  try {
    const extensions = await chrome.management.getAll();

    const proxyExtensions = extensions.filter(
      (ext) =>
        ext.id !== chrome.runtime.id &&
        ext.enabled &&
        ext.permissions?.includes('proxy'),
    );

    if (proxyExtensions.length > 0) {
      const controllerExt = proxyExtensions[0];

      const theBiggestIcon = controllerExt.icons?.reduce((acc, icon) => {
        return icon.size > acc.size ? icon : acc;
      }, controllerExt.icons?.[0]);

      return {
        hasConflictingExtension: true,
        type: 'proxy',
        conflictingExtensionId: controllerExt.id,
        conflictingExtensionName: controllerExt.name,
        conflictingExtensionIcon: theBiggestIcon?.url,
      };
    }

    return { hasConflictingExtension: false, type: 'proxy' };
  } catch (error) {
    console.error('Error detecting proxy controller:', error);
    return { hasConflictingExtension: false, type: 'proxy' };
  }
}

async function detectInternalExtensions(): Promise<DetectedConflictingExtensions> {
  try {
    const extensions = await chrome.management.getAll();
    const internalExtensions = extensions.filter(
      (ext) =>
        ext.id !== chrome.runtime.id &&
        ext.enabled &&
        knownExtensions.internalExtensions.some(
          (internal) => internal.id === ext.id,
        ),
    );

    if (internalExtensions.length > 0) {
      const otherInternalExt = internalExtensions[0];

      const theBiggestIcon = otherInternalExt.icons?.reduce((acc, icon) => {
        return icon.size > acc.size ? icon : acc;
      }, otherInternalExt.icons?.[0]);

      return {
        hasConflictingExtension: true,
        type: 'internal',
        conflictingExtensionId: otherInternalExt.id,
        conflictingExtensionName: otherInternalExt.name,
        conflictingExtensionIcon: theBiggestIcon?.url,
      };
    }

    return { hasConflictingExtension: false, type: 'internal' };
  } catch (error) {
    console.error('Error detecting internal extensions:', error);
    return { hasConflictingExtension: false, type: 'internal' };
  }
}

async function detectVpnExtensions(): Promise<DetectedConflictingExtensions> {
  try {
    const extensions = await chrome.management.getAll();
    const vpnExtensions = extensions.filter(
      (ext) =>
        ext.id !== chrome.runtime.id &&
        ext.enabled &&
        knownExtensions.vpnExtensions.some(
          (knownExt) => knownExt.id === ext.id,
        ),
    );

    if (vpnExtensions.length > 0) {
      const otherVpnExt = vpnExtensions[0];

      const theBiggestIcon = otherVpnExt.icons?.reduce((acc, icon) => {
        return icon.size > acc.size ? icon : acc;
      }, otherVpnExt.icons?.[0]);

      return {
        hasConflictingExtension: true,
        type: 'vpn',
        conflictingExtensionId: otherVpnExt.id,
        conflictingExtensionName: otherVpnExt.name,
        conflictingExtensionIcon: theBiggestIcon?.url,
      };
    }

    return { hasConflictingExtension: false, type: 'vpn' };
  } catch (error) {
    console.error('Error detecting VPN extensions:', error);
    return { hasConflictingExtension: false, type: 'vpn' };
  }
}

async function handleExtensionMonitoring(): Promise<void> {
  const result: ExtensionMonitoringData = {
    proxy: {
      hasConflictingExtension: false,
      type: 'proxy',
    },
    internal: {
      hasConflictingExtension: false,
      type: 'internal',
    },
    vpn: {
      hasConflictingExtension: false,
      type: 'vpn',
    },
  };

  const proxyResult = await detectProxyController();

  if (proxyResult.hasConflictingExtension) {
    console.warn(
      'Proxy controlled by other extension:',
      proxyResult.conflictingExtensionName || 'unknown',
    );

    result.proxy = proxyResult;

    analytics.trackUserAction(
      'proxy_controlled_by_other_extension',
      proxyResult.conflictingExtensionName || 'unknown',
      proxyResult.conflictingExtensionId,
    );
  }

  const internalResult = await detectInternalExtensions();

  if (internalResult.hasConflictingExtension) {
    console.warn(
      'Another internal extension detected:',
      internalResult.conflictingExtensionName || 'unknown',
    );

    result.internal = internalResult;

    analytics.trackUserAction(
      'internal_extension_conflict',
      internalResult.conflictingExtensionName || 'unknown',
      internalResult.conflictingExtensionId,
    );
  }

  const vpnResult = await detectVpnExtensions();

  if (vpnResult.hasConflictingExtension) {
    console.warn(
      'Another VPN extension detected:',
      vpnResult.conflictingExtensionName || 'unknown',
    );

    result.vpn = vpnResult;

    analytics.trackUserAction(
      'vpn_extension_conflict',
      vpnResult.conflictingExtensionName || 'unknown',
      vpnResult.conflictingExtensionId,
    );
  }

  await setLocalStorageData({ [LocalStorageKey.ExtensionMonitoring]: result });
}

export async function initExtensionMonitoring() {
  chrome.management.onInstalled.addListener(handleExtensionMonitoring);
  chrome.management.onUninstalled.addListener(handleExtensionMonitoring);
  chrome.management.onEnabled.addListener(handleExtensionMonitoring);
  chrome.management.onDisabled.addListener(handleExtensionMonitoring);

  chrome.alarms.onAlarm.addListener(async (alarm) => {
    if (alarm.name === Alarms.ExtensionMonitoring) {
      await handleExtensionMonitoring();
    }
  });

  const periodInMinutes = process.env.MONITORING_EXTENSION_PERIOD_IN_MINUTES
    ? parseInt(process.env.MONITORING_EXTENSION_PERIOD_IN_MINUTES, 10)
    : 30;

  chrome.alarms.create(Alarms.ExtensionMonitoring, {
    delayInMinutes: 0, // It means that the alarm will be created immediately
    periodInMinutes,
  });
}
